.filter-tags-button-container {
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  margin-bottom: 0;
  background: #fff;
  box-shadow: 0 1px 5px rgba(0, 0, 0, 0.2), 0 3px 1px rgba(0, 0, 0, 0.12),
    0 2px 2px rgba(0, 0, 0, 0.14);
  padding: 18px 16px;
  color: #757575;
  font-size: 14px;
  display: flex;
  align-items: center;
}

.filter-tags-button-text {
  margin-left: 16px;
}

.offcanvas-container {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background: #fff;
  z-index: 2147483650;
  overflow: scroll;
}

.offcanvas-header-c {
  display: flex;
  background: #fff;
  box-shadow: 0 1px 5px rgb(0 0 0 / 20%), 0 3px 1px rgb(0 0 0 / 12%),
    0 2px 2px rgb(0 0 0 / 14%);
  font-weight: 500;
  font-size: 18px;
  color: #757575;
  padding: 12px 20px;
}

.offcanvas-header-text {
  margin-left: 20px;
}

.offcanvas-body-c {
  display: flex;
  margin: 38px 16px;
  flex-wrap: wrap;
  height: 68%;
  overflow: scroll;
}

.tag-mobile {
  color: #424242;
  font-size: 14px;
  padding: 6px 12px;
  font-weight: 400;
  border-radius: 2px;
  margin-right: 16px;
  margin-bottom: 10px;
  background: #eee;
}

.offcanvas-apply-button {
  background: linear-gradient(95.95deg, #f6430f 0.78%, #ee9f15 99.43%);
  box-shadow: 0 1px 5px rgb(0 0 0 / 20%), 0 3px 1px rgb(0 0 0 / 12%),
    0 2px 2px rgb(0 0 0 / 14%);
  position: absolute;
  bottom: 0;
  width: 100%;
  padding: 13px 0;
  text-align: center;
  color: #fff;
  font-weight: 600;
  font-size: 20px;
}

.fullscreen-modal.modal-dialog {
  position: absolute;
  top: 40px;
  left: 40px;
  right: 40px;
  bottom: 40px;
  background-color: papayawhip;
}

.fullscreen-modal .modal-content {
  position: fixed;
  top: 50px;
  left: 50px;
  right: 50px;
  bottom: 50px;
  background-color: rebeccapurple;
}
