.events-container {
  font-weight: 400;
  border: 1px solid #d0d0d0;
  background: #fff;
  border-radius: 6px;
}

.events-outer-container {
  padding: 70px 130px;
}
@media (max-width: 1150px) and (min-width: 721px) {
  .events-outer-container {
    padding: 70px 60px;
  }
}

@media (max-width: 720px) {
  .events-outer-container {
    padding: 70px 20px;
  }
}

.events-tags-container {
  /* width: 100%; */
  /* padding: 70px 130px; */
  margin: 24px;
  display: flex;
  /* justify-content: center; */
}

.events-cards-list-outer-container {
  width: 80%;
}

.events-tags-outer-container {
  width: 20%;
}

@media (max-width: 768px) {
  .events-cards-list-outer-container {
    width: 90%;
  }
  .events-tags-outer-container {
    display: none;
  }
  .events-tags-container {
    justify-content: center;
    margin: 0;
  }
}
